.modal {
    height: 100vh;
    width: 100vw;
    background-color: rgba(0,0,0,0.4);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    transition: 0.5s;
    color: black;
}

.modal.active {
  opacity: 1;
    pointer-events: all;
}

.modalContent {
  padding: 20px;
    border-radius: 12px;
    background-color: white;
    height: 400px;
    width: 400px;
    transform: scale(0.5);
    transition: 0.4s all;
}

.modalContent.active {
    transform: scale(1);
}